@import "../icons/sass-inline-svg.scss";

footer{
  background-color: $rojo;

  .logo{
    position: relative;
    padding-top: 35px;

    .footer-icon{
      display: block;
      width: 70px;
      position: absolute;
      left: 0;
      right: 0;
      margin: auto;
      top: -35px;

      &.footer-metro-icon{
        height: 75px;
        width: 75px;
        background: url(inline-svg("metro", $blanco, $url:false)) no-repeat;
      }
    }
  }

  .promesas-valor{
    background: $gris;
    margin-top: 30px;
    >a{
      display: inline-block;
      box-sizing: border-box;
      width: 50%;
      padding: 20px 0;
      border-bottom:1px solid $gris-borde;
      color: $negro;
      text-align: center;

      &:nth-last-child(2), &:last-child{
        border-bottom:none;
      }
      &:nth-child(odd){
        border-right:1px solid $gris-borde;
      }
      i{
        display: inline-block;
        vertical-align: middle;
        font-size: 34px;
      }
      span{
        display: inline-block;
        vertical-align: middle;
        text-align: left;
        font-family: $Lato-Bold;
        margin-left: 2px;
        text-transform: uppercase;
        height: 100%;
        width: 50%;
      }
    }
  }

  .newsletter{
    height: 195px;
    background: url("../../assets/img/newsletter-mobile.jpg") no-repeat center;
    background-size: cover;
    text-align: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,.15);

    .newsletter-header{
      color: $rojo;
      padding-top: 17px;
      i{
        font-size: 24px;
      }
      h2{
        font-family: $Lato-Bold;
        font-size: 14px;
        margin-bottom: 8px;
        margin-top: 0;
      }
      span{
        font-family: $Lato-Regular;
        color: $negro;
        display: none;
      }
    }
    #newsletter-form{
      width: 65%;
      margin: auto;
      .input-container{
        padding: 2px 0;
        #email{
          padding: 5px;
          -webkit-box-sizing: border-box;
          -moz-box-sizing: border-box;
          box-sizing: border-box;
        }
        #send{
          font-family: $Lato-Bold;
          background-color: $amarillo;
          color: $rojo;
          border: none;
          padding: 5px;
        }

        input[type="text"], input[type="submit"]{
          width: 100%;
        }
      }
      .conditions-text{
        text-align: left;
        font-family: $Lato-Regular;
        font-size: 11px;
        input[type="checkbox"]{
          margin-left: 0;
        }
        a{
          color: $negro;
          text-decoration: underline;
        }
      }
    }
  }

  .static-links{
    .links-section{
      border-bottom: $rojo-medio 1px solid;
      .links-header{
        padding: 9px 15px;
        margin: 0;
        color: $amarillo;
        font-family: $Lato-Bold;
        font-size: $font-size;
        span{
          float: right;
        }
      }
      .links-list{
        padding: 0 25px 15px 25px;
        a{
          font-family: $Lato-Regular;
          display: block;
          color: $blanco;
          font-size: 12px;
        }
      }
    }
  }

  .social-media{
    height: 54px;
    font-size: 20px;
    text-align: center;
    a{
      color: $blanco;
      line-height: 54px;
      padding: 0 5px;
      &:hover{
        color: $amarillo;
      }
    }
  }

  .legals{
    background-color: $rojo-oscuro;
    color: $blanco;
    height: 50px;
    line-height: 25px;
    text-align: center;
    font-family: $Lato-Light;
    font-size: 10px;
    .pipeline{
      display: none;
      margin: 0 3px;
    }
    a{
      display: inline-block;
      position: relative;
      vertical-align: text-bottom;
      margin: 0 2px;
      &.blacksip-icon{
        height: 13px;
        width: 50px;
        background: url(inline-svg("blacksip", $blanco, $url:false)) no-repeat;
      }
      &.vtex-icon{
        height: 16px;
        width: 43px;
        background: url(inline-svg("vtex", $blanco, $url:false)) no-repeat;
      }
    }
  }
}

@media screen and (min-width: $tablet)  {
  footer{
    .logo {
      padding-top: 24px;
      .footer-icon {
        &.footer-metro-icon {
          height: 100px;
          width: 100px;
        }
      }
    }
    .promesas-valor {
      > a {
        width: 25%;
        border: 0;
        &:nth-child(odd){
          border: 0;
        }
      }
    }

    .newsletter {
      height: 160px;
      background: url("../../assets/img/newsletter-tablet.jpg") no-repeat center;
      background-size: cover;
      position: relative;

      .form-container{
        width: 50%;
        height: 90px;
        right: 0;
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;

        .newsletter-header{
          width: 44%;
          display: inline-block;
          position: relative;
          text-align: left;
          padding-top: 0;
          vertical-align: top;
          i{
            font-size: 29px;
            position: absolute;
            left: -36px;
          }
          span{
            display: block;
          }
        }
        #newsletter-form {
          display: inline-block;
          width: 55%;
          padding-right: 15px;
          box-sizing: border-box;
          vertical-align: top;
        }
      }
    }

    .static-links {
      .links-container{
        display: inline-block;
        text-align: center;
        width: 45%;
        vertical-align: top;

        &:nth-child(1){
          margin-right: 9%;
        }

        .links-section {
          display: inline-block;
          width: 48%;
          border: 0;
          vertical-align: top;
          box-sizing: border-box;

          .links-header{
            text-align: left;
            padding: 9px 0 9px 15px;
          }
          .links-list{
            text-align: left;
            padding: 5px 15px;
          }
        }
      }
    }

    .legals{
      height: 28px;
      line-height: 28px;
      div{
        display: inline-block;
      }
      .pipeline{
        display: inline-block;
      }
    }
  }
}

@media screen and (min-width: $desktop)  {
  footer {
    .newsletter{
      background: url("../../assets/img/newsletter-desktop.jpg") no-repeat center;
      background-size: cover;

      .form-container{
        width: 45%;
        padding-right: 73px;
        box-sizing: border-box;

        .newsletter-header{
          i{
            font-size: 35px;
            left: -42px;

          }
          h2{
            font-size: 20px;
          }
          span{
            font-family: $Lato-Bold;
          }
        }
      }
    }

    .static-links {
      .links-container {
        .links-section {
          padding: 0 5%;
        }
      }
    }

    .logo {
      .footer-icon {
        &.footer-metro-icon {
          height: 130px;
          width: 130px;
        }
      }
    }
  }
}