.desktop-menu{
  display: inline-block;
  padding-left: 10px;
  box-sizing: border-box;
  cursor: pointer;
  width: 15%;

  .button-text{
    margin: 0 5px;
  }

  #desktop-menu-container{
    opacity: 0;
    position: absolute;
    z-index: 100;
    width: 100%;
    left: 0;

    .blackbox{
      position: absolute;
      width: 100%;
      height: 100vh;
      background-color: $negro-fondo;
      z-index: 101;
    }
    .menu-content-desktop{
      position: absolute;
      width: 100%;
      background-color: $blanco;
      z-index: 102;

      a{
        display: block;
        color: $negro;
      }

      .menuDepartments{
        display: inline-block;
        width: 234px;
        background-color: $gris;
        vertical-align: top;
        padding: 10px 0;

        .department{
          >a{
            height: 35px;
            padding-left: 30px;
            position: relative;

            &:after{
              content: " ";
              position: absolute;
              right: 5px;
            }
          }
          .menuCategories{
            opacity: 0;
            width: calc(100% - 234px);
            vertical-align: top;
            position: absolute;
            right: 0;
            top: 0;
            -webkit-transition: opacity 100ms ease;
            -moz-transition: opacity 100ms ease;
            -ms-transition: opacity 100ms ease;
            -o-transition: opacity 100ms ease;
            transition: opacity 100ms ease;
            pointer-events: none;

            .subMenuContainer{
              padding: 44px;
              .category{
                display: inline-block;
                width: calc(100% / 3 - 5px);
                vertical-align: top;
                margin-bottom: 44px;

                a{
                  height: 20px;
                  line-height: 20px;

                  &:hover{
                    color: $rojo;
                  }

                  &.title{
                    font-family: $Lato-Bold;
                  }

                  &:not(.title){
                    margin-left: 5px;
                  }
                }
              }
            }
          }

          &:hover{
            >a{
              font-family: $Lato-Bold;
              background-color: $blanco;

              &:after{
                font-family: 'icomoon' !important;
                content: "\e933";
              }
            }

            .menuCategories{
              opacity: 1;
              pointer-events: auto;
            }
          }
        }
      }
    }
  }
}