.main-shelf{
  background:$gris;
  text-align:center;
  margin-bottom:38px;
  padding: 10px 0;
  h2{
    font-family:$Lato-Bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 18px;
    display: block;
  }
  .slick-dots {
    margin-top: 30px;
  }
}

.container-vitrine{
  background:$gris;
  .vitrine{
    background: $blanco;
    margin:0 1%;
    padding: 5px;
    display: inline-block;
    border:1px solid $gris-borde;
    .image{
      width: 39%;
      display: inline-block;
      vertical-align: middle;
      box-sizing: border-box;
      position: relative;

      .tag-discount{
        position: absolute;
        top:0;
        left:0;
        background: $blanco;
        border-radius:50%;
        text-align: center;
        width:36px;
        height:36px;
        -webkit-box-shadow: 0 0 1px 1px rgba(0,0,0,0.2);
        -moz-box-shadow: 0 0 1px 1px rgba(0,0,0,0.2);
        box-shadow: 0 0 1px 1px rgba(0,0,0,0.2);

        img{
          width: 100%;
        }

        span{
          background: $amarillo;
          border-radius:50%;
          width:28px;
          height:28px;
          line-height:28px;
          color:$rojo;
          font-family:$Lato-Bold;
          display: inline-block;
          margin: 4px;
          font-size: 12px;
        }
      }
    }
    .info-product{
      width: 60%;
      display: inline-block;
      vertical-align: middle;
      box-sizing: border-box;
      font-family: $Lato-Light;
      font-size: 11px;
      text-align: left;

      h3{
        margin:0;
        font-weight: bold;
      }
      .brand{
        display: block;
        font-size:10px;
      }
      .prices{
        margin-top:10px;
        padding-bottom: 10px;
        .list-price{
          font-size:12px;
          text-decoration: line-through;
          color: $gris-claro;
        }
        .best-price{
          font-size:14px;
          font-family:$Lato-Bold;
          margin-left:10px;
        }
        .price-cencosud{
          margin-top: 10px;
          padding-top: 10px;
          border-top:1px solid $gris-borde;
          img{
            display: inline-block;
            vertical-align: middle;
          }
          .price{
            display: inline-block;
            vertical-align: middle;
            font-family:$Lato-Bold;
            font-size:16px;
            color:$naranja;
            margin-left: 5px;
          }
        }
      }
    }
    .content-buy{
      height: 30px;
      .quantity{
        display: inline-block;
        height: 100%;
        vertical-align: middle;

        input[type="text"]{
          display: inline-block;
          width: 42px;
          height: 100%;
          vertical-align: middle;
          border: none;
          box-shadow: none;
          border-radius: 0;
          background: $gris-qty;
          color: $negro;
          font-size: 16px;
          text-align: center;
          border-right: 1px solid $gris-line;
          box-sizing: border-box;
          margin-left: -3px;
        }
        .btns-qty{
          display: inline-block;
          width: 25px;
          height: 100%;
          vertical-align: middle;
          span{
            display: block;
            height: 50%;
            line-height: 11px;
            background: $gris-qty;
            cursor: pointer;
            padding: 2px;
            &.plus-qty{
              border-bottom: 1px solid $gris-line;
              height: 50%;
              box-sizing: border-box;
            }
            &.minus-qty{
              height: 50%;
              box-sizing: border-box;
            }
          }
        }
      }
      .btn-buy{
        display: inline-block;
        width: calc(98% - 68px);
        vertical-align: middle;
        height: 100%;
        background:$rojo;
        color:$blanco;
        font-family: $Lato-Bold;
        letter-spacing: 1px;
        font-size:13px;
        border:none;
        padding: 0;
        line-height: 30px;
      }
    }
  }
}

@media screen and (min-width: $tablet)  {
  .container-vitrine {
    .vitrine {
      .image {
        width: 100%;
        padding: 0 10px;
      }
      .info-product {
        width: 100%;
        padding: 0 10px;
        text-align: center;
      }
    }
  }
}