.menu-content{
  display: inline-block;
  box-sizing: border-box;
  float: right;
  padding: 10px 15px 0 0;
  a{
    font-family: $Lato-Regular;
    color: $negro;
  }
  #mainMenuContainer {
    display: block;
    position: fixed;
    z-index: 90;
    width: 0;
    height: fit-content;
    height: -moz-fit-content;
    height: -webkit-fit-content;
    max-height: 430px;
    top: 25px;
    right: 0;
    bottom: 0;
    transition: all 0.3s cubic-bezier(0.21, 1, 0.55, 1);
    pointer-events: none;
    overflow: hidden;
    padding: 10px 0;
    opacity: 0;
    background-color: $blanco-fondo;

    #mainMenu {
      float: right;
      overflow: hidden;
      transition: all 0.5s ease-out;
      display: block;
      position: relative;
      opacity: 1;
      box-sizing: border-box;
      z-index: 91;
      width: 100%;
      max-width: 350px;
      #mainMenuHead {
        .utilityBtns {
          width: 100%;
          .closeMenu {
            height: 100%;
            text-align: center;
            background: none;
            vertical-align: middle;
            display: inline-block;
          }
        }
      }
      #mainMenuFooter, #mainMenuHead {
        display: none;
        padding: 15px;
        width: 100%;
      }
      #mainMenuBody, #mainMenuFooter {
        width: 310px;
        position: relative;
        left: 0;
        transition: all 0.4s cubic-bezier(0.21, 1, 0.55, 1);
        a {
          font-size: 0.7rem;
          display: block;
          padding: 7px 0;
          letter-spacing: 0.04rem;
          position: relative;
          text-transform: uppercase;
          &:not(.redirect) {
            &:after {
              font-family: 'icomoon';
              content: '\e978';
              font-size: 0.75rem;
              position: absolute;
              line-height: 12px;
              right: 0;
            }
          }
        }
      }
      #mainMenuBody {
        width: 300%;
        transition: all 0.5s cubic-bezier(0.21, 1, 0.55, 1) 0s;
        a {
          transition: all 0.4s cubic-bezier(0.21, 1, 0.55, 1);
          font-size: 0.75rem;
          display: block;
          position: relative;
          left: 50px;
          opacity: 0;
          text-transform: none;
          &:not(.redirect) {
            &:after {
              font-family: 'icomoon';
              content: '\e978';
              position: absolute;
              line-height: 30px;
              display: block;
              top: 0;
              right: 0;
              font-size: 0.65rem;
            }
          }
          strong {
            font-weight: bold;
          }
        }
        .subMenuContiner {
          width: 100%;
          position: absolute;
          top: 0;
          left: 0;
          box-sizing: border-box;
          opacity: 0;
          pointer-events: none;
          a {
            padding-left: 25px;
          }
          &.showSubMenu {
            opacity: 1;
            z-index: 92;
            pointer-events: auto;
            position: relative;
            a {
              left: 0;
              opacity: 1;
            }
          }
          .titleBackBtn {
            display: block;
            width: 100%;
            height: 15px;
            margin: 10px 0;
            cursor: pointer;
            span {
              vertical-align: middle;
            }
            .titleSubmenu {
              font-size: 0.7rem;
              text-transform: uppercase;
              display: inline-block;
              width: calc(100% - 40px);
            }
          }
        }
        .back-icon {
          position: relative;
          display: inline-block;
          width: 20px;
          height: 100%;
          text-align: center;
          margin-right: 5px;
          &:after {
            font-family: 'icomoon';
            content: '\e978';
            display: block;
            font-size: 12px;
            transform: rotate(-180deg);
            text-align: center;
          }
        }
      }
      .menuDepartments, .menuCategories, .menuSubCategories {
        display: inline-block;
        width: calc(33% - 1px);
        box-sizing: border-box;
        padding: 0 20px;
        position: relative;
        vertical-align: top;
      }
    }

    &.openMenu {
      pointer-events: auto;
      width: 310px;
      opacity: 1;
      #mainMenu {
        #mainMenuBody {
          .menuDepartments  {
            position: relative;
            a {
              left: 0;
              opacity: 1;
            }
          }
        }
      }
    }
    &.openSubMenu {
      #mainMenu {
        #mainMenuBody {
          left: -100%;
        }
      }
    }
    &.openMenuSubCats {
      #mainMenu {
        #mainMenuBody {
          left: -200%;
        }
      }
    }
  }
}

//TABLET LANDSCAPE
@media screen and (max-width: $tabletLandscape)  {
  .menu-content{
    #mainMenuContainer {
      line-height: initial;
      height: 100%;
      right: 0;
      top: 0;
      max-height: none;
      max-width: none;
      padding: 0;
      &.openMenu {
        width: 100%;
        background-color: rgba(0,0,0,0.7);
      }
      #mainMenu {
        background-color: white;
        top: 0;
        bottom: 0;
        #mainMenuHead {
          //padding:0;
          .utilityBtns {
            .closeMenu {
              font-size: 0.7rem;
              position:relative;
              left:-12px;
              top:-10px;
            }
          }
        }
        .subMenuContiner {
          padding: 0;
          .titleBackBtn {
            .back-icon {
              width: 14px;
            }
            .titleSubmenu {
              padding-left:10px;
            }
          }
          a {
            padding:7px 0 7px 26px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: $tabletPortrait)  {
  .menu-content{
    #mainMenuContainer {
      #mainMenu {
        max-width: 270px;
        #mainMenuHead {
          padding-bottom: 15px;
          display: block;
          .utilityBtns {
            .closeMenu {
              left:0;
              top:0;
            }
          }
        }
        #mainMenuFooter {
          display: block;
        }
      }
      .openMenu {
        background-color: rgba(0,0,0,0);
      }
    }
  }
}

@media screen and (max-width: $mobile) {
  .menu-content{
    #mainMenuContainer {
      max-width: initial;
      #mainMenu {
        height: 100%;
        #mainMenuHead {
          display: block;
        }
      }
    }
  }
}

@media screen and (min-width: $tablet)  {
  .menu-content {
    margin-left: 15px;
    float: left;
    color: $rojo;
    height: 100%;
    #mainMenuContainer {
      right: auto;
      left: 0;
      #mainMenu {
        float: left;
      }
    }
  }
}

@media screen and (min-width: $desktop)  {
  .menu-content {
    display: none;
  }
}