@import "../utilities/variables";
@import "../icons/sass-inline-svg.scss";

header{
	background: $rojo;
	border-bottom:1px solid $amarillo;
	height:  70px;
	position: relative;
	font-family: $Lato-Regular;

	.header-first-row{
		display: none;
		height: 26px;
		padding: 0 17px;
		border-bottom:1px solid $gris-separador;
		font-size: 10px;
		line-height: 26px;

		.contacts-number{
			display: inline-block;

			.phone-title{
				color: $gris-claro;
				margin: 0 3px;
			}
			.phone-number{
				color: $rojo;
				margin-right: 5px;
			}
		}

		.header-options{
			display: inline-block;
			float: right;
			a{
				color: $gris-claro;
				padding-left: 30px;
				span{
					padding-left: 4px;
				}
			}
		}
	}

	.header-second-row{
		position: relative;
		width: 100%;
		height: 100%;
		display: block;

		#search-container{
			display: block;
			position: absolute;
			bottom: 0;
			background: $gris;
			font-size: 12px;
			height: 30px;
			width: 100%;
			text-align: center;
			input[type="text"]{
				border: none;
				font-family: $Lato-Regular;
				text-align: center;
				height: 100%;
				width: 50%;
				background: $gris;
				letter-spacing: 1px;
				outline: 0;
				padding:0;
				&::-webkit-input-placeholder {
					color: $gris-oscuro;
				}
				&::-moz-placeholder {
					color: $gris-oscuro;
				}
				&:-ms-input-placeholder {
					color: $gris-oscuro;
				}
				&:-moz-placeholder {
					color: $gris-oscuro;
				}
			}
			.search-icon{
				margin-right: 5px;
			}
		}
		.container-logo-search {
			width: 100%;
			.header-metro-icon{
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				height: 75px;
				width: 75px;
				background: url(inline-svg("metro", $blanco, $url:false)) no-repeat;
			}
		}
		.cart-content{
			display: inline-block;
			float: right;
			position: relative;
			box-sizing: border-box;
			padding: 11px 20px 7px 10px;
			.square{
				position: absolute;
				right: 10px;
				top: 0;
			}
		}
		.iconsLat{
			height: 100%;
			i{
				color: #fff;
				font-size:20px;
			}
			a{
				display: block;
				position: relative;
				.square{
					background-color: $blanco;
					font-family: $OpenSans-Semibold;
					border-radius: 50%;
					color: #DF0209;
					font-size: 10px;
					height: 16px;
					line-height: 16px;
					text-align: center;
					width: 16px;
					top: -7px;
					right: -7px;
				}
			}
		}
		.my-list, .search-by-list, .recipes{
			display: none;
			float: right;
			margin-right: 28px;
			height: 100%;
			color: $negro;

			i{
				display: inline-block;
				vertical-align: middle;
				font-size: 24px;
			}
			span{
				display: inline-block;
				vertical-align: middle;
			}
		}
	}

	.header-third-row{
		display: none;
		height: 40px;
		width: 100%;
		box-sizing: border-box;
		line-height: 40px;
		background-color: $rojo;
		color: $blanco;
		padding: 0 30px;

		.header-static-links{
			display: inline-block;
			text-align: center;
			height: 100%;

			a{
				color: $blanco;
				display: inline-block;
				height: 100%;
				background-color: $rojo;

				-webkit-transition: background-color 100ms ease;
				-moz-transition: background-color 100ms ease;
				-ms-transition: background-color 100ms ease;
				-o-transition: background-color 100ms ease;
				transition: background-color 100ms ease;

				&:hover{
					background-color: $rojo-medio;
				}
			}
		}
	}
}

//TABLET
@media screen and (min-width: $tablet)  {
	header{
		background: $gris;
		border-bottom: 2px solid $rojo;
		height: auto;

		.header-first-row {
			display: block;
		}

		.header-second-row{
			height: 73px;
			line-height: 73px;
			
			#search-container{
				display: inline-block;
				width: 50%;
				bottom: auto;

				.search-icon{
					position: relative;
					display: inline-block;
					background: $rojo;
					color: $blanco;
					border-radius: 50%;
					height: 32px;
					width: 32px;
					vertical-align: middle;
					font-size: 15px;
					line-height: 32px;
					left: 15px;
				}

				input[type="text"]{
					display: inline-block;
					border-top: 1px solid $gris-qty;
					width: calc(100% - 90px);
					background-color: $blanco;
					vertical-align: middle;
				}
			}

			.container-logo-search {
				height: 100%;
				.header-metro-icon{
					position: absolute;
					bottom: 0;
					right: 0;
					top: auto;
					margin: auto;
					height: 110px;
					width: 110px;
					background-position-y: 10px;
				}
			}

			.cart-content{
				margin-right: 15px;
			}

			.my-list, .search-by-list {
				display: inline-block;
			}

			.iconsLat{
				padding: 0;
				i{
					color: $rojo;
				}
				a{
					.square{
						top: 14px;
						background-color: $rojo;
						color: $blanco;
					}
				}
			}
		}
	}
}

@media screen and (min-width: $desktop)  {
	header{
		border-bottom: 2px solid $amarillo;

		.header-first-row {
			padding: 0 17px 0 140px;
		}

		.header-second-row {
			.container-logo-search {
				.header-metro-icon {
					right: auto;
					left: 20px;
				}
			}
			#search-container{
				height: 45px;
				width: 60%;
				.search-icon{
					left: 115px;
				}
			}

			.recipes {
				display: inline-block;
			}
		}

		.header-third-row{
			display: block;
			.header-static-links{
				width: 80%;
				text-align: center;
				a{
					width: calc(99% / 3);
				}
			}
		}
	}
}

@import "menuMobile";
@import "menuDesktop";