header .header-second-row .cart-content {
  display: inline-block;
  float: right;
  position: relative;
  box-sizing: border-box;
  padding: 11px 20px 7px 10px;
}

header .header-second-row .cart-content .square {
  position: absolute;
  right: 10px;
  top: 0;
}

header .header-second-row .iconsLat {
  height: 100%;
}

header .header-second-row .iconsLat i {
  color: #fff;
  font-size: 20px;
}

header .header-second-row .iconsLat a {
  display: block;
  position: relative;
}

header .header-second-row .iconsLat a .square {
  background-color: #FFFFFF;
  font-family: "OpenSans-Semibold";
  border-radius: 50%;
  color: #DF0209;
  font-size: 10px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  width: 16px;
  top: -7px;
  right: -7px;
}

header .header-second-row .my-list, header .header-second-row .search-by-list, header .header-second-row .recipes {
  display: none;
  float: right;
  margin-right: 28px;
  height: 100%;
  color: #000000;
}

header .header-second-row .my-list i, header .header-second-row .search-by-list i, header .header-second-row .recipes i {
  display: inline-block;
  vertical-align: middle;
  font-size: 24px;
}

header .header-second-row .my-list span, header .header-second-row .search-by-list span, header .header-second-row .recipes span {
  display: inline-block;
  vertical-align: middle;
}

header .header-third-row {
  display: none;
  height: 40px;
  width: 100%;
  box-sizing: border-box;
  line-height: 40px;
  background-color: #DF0209;
  color: #FFFFFF;
  padding: 0 30px;
}

header .header-third-row .header-static-links {
  display: inline-block;
  text-align: center;
  height: 100%;
}

header .header-third-row .header-static-links a {
  color: #FFFFFF;
  display: inline-block;
  height: 100%;
  background-color: #DF0209;
  -webkit-transition: background-color 100ms ease;
  -moz-transition: background-color 100ms ease;
  -ms-transition: background-color 100ms ease;
  -o-transition: background-color 100ms ease;
  transition: background-color 100ms ease;
}

header .header-third-row .header-static-links a:hover {
  background-color: #A70207;
}

@media screen and (min-width: 767px) {
  header {
    background: #f2f2f2;
    border-bottom: 2px solid #DF0209;
    height: auto;
  }
  header .header-first-row {
    display: block;
  }
  header .header-second-row {
    height: 73px;
    line-height: 73px;
  }
  header .header-second-row #search-container {
    display: inline-block;
    width: 50%;
    bottom: auto;
  }
  header .header-second-row #search-container .search-icon {
    position: relative;
    display: inline-block;
    background: #DF0209;
    color: #FFFFFF;
    border-radius: 50%;
    height: 32px;
    width: 32px;
    vertical-align: middle;
    font-size: 15px;
    line-height: 32px;
    left: 15px;
  }
  header .header-second-row #search-container input[type="text"] {
    display: inline-block;
    border-top: 1px solid #E6E6E6;
    width: calc(100% - 90px);
    background-color: #FFFFFF;
    vertical-align: middle;
  }
  header .header-second-row .container-logo-search {
    height: 100%;
  }
  header .header-second-row .container-logo-search .header-metro-icon {
    position: absolute;
    bottom: 0;
    right: 0;
    top: auto;
    margin: auto;
    height: 110px;
    width: 110px;
    background-position-y: 10px;
  }
  header .header-second-row .cart-content {
    margin-right: 15px;
  }
  header .header-second-row .my-list, header .header-second-row .search-by-list {
    display: inline-block;
  }
  header .header-second-row .iconsLat {
    padding: 0;
  }
  header .header-second-row .iconsLat i {
    color: #DF0209;
  }
  header .header-second-row .iconsLat a .square {
    top: 14px;
    background-color: #DF0209;
    color: #FFFFFF;
  }
}

@media screen and (min-width: 1349px) {
  header {
    border-bottom: 2px solid #FFED00;
  }
  header .header-first-row {
    padding: 0 17px 0 140px;
  }
  header .header-second-row .container-logo-search .header-metro-icon {
    right: auto;
    left: 20px;
  }
  header .header-second-row #search-container {
    height: 45px;
    width: 60%;
  }
  header .header-second-row #search-container .search-icon {
    left: 115px;
  }
  header .header-second-row .recipes {
    display: inline-block;
  }
  header .header-third-row {
    display: block;
  }
  header .header-third-row .header-static-links {
    width: 80%;
    text-align: center;
  }
  header .header-third-row .header-static-links a {
    width: calc(99% / 3);
  }
}

.menu-content {
  display: inline-block;
  box-sizing: border-box;
  float: right;
  padding: 10px 15px 0 0;
}

.menu-content a {
  font-family: "Lato-Regular";
  color: #000000;
}

.menu-content #mainMenuContainer {
  display: block;
  position: fixed;
  z-index: 90;
  width: 0;
  height: fit-content;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  max-height: 430px;
  top: 25px;
  right: 0;
  bottom: 0;
  transition: all 0.3s cubic-bezier(0.21, 1, 0.55, 1);
  pointer-events: none;
  overflow: hidden;
  padding: 10px 0;
  opacity: 0;
  background-color: rgba(255, 255, 255, 0.7);
}

.menu-content #mainMenuContainer #mainMenu {
  float: right;
  overflow: hidden;
  transition: all 0.5s ease-out;
  display: block;
  position: relative;
  opacity: 1;
  box-sizing: border-box;
  z-index: 91;
  width: 100%;
  max-width: 350px;
}

.menu-content #mainMenuContainer #mainMenu #mainMenuHead .utilityBtns {
  width: 100%;
}

.menu-content #mainMenuContainer #mainMenu #mainMenuHead .utilityBtns .closeMenu {
  height: 100%;
  text-align: center;
  background: none;
  vertical-align: middle;
  display: inline-block;
}

.menu-content #mainMenuContainer #mainMenu #mainMenuFooter, .menu-content #mainMenuContainer #mainMenu #mainMenuHead {
  display: none;
  padding: 15px;
  width: 100%;
}

.menu-content #mainMenuContainer #mainMenu #mainMenuBody, .menu-content #mainMenuContainer #mainMenu #mainMenuFooter {
  width: 310px;
  position: relative;
  left: 0;
