footer .promesas-valor {
  background: #f2f2f2;
  margin-top: 30px;
}

footer .promesas-valor > a {
  display: inline-block;
  box-sizing: border-box;
  width: 50%;
  padding: 20px 0;
  border-bottom: 1px solid #999999;
  color: #000000;
  text-align: center;
}

footer .promesas-valor > a:nth-last-child(2), footer .promesas-valor > a:last-child {
  border-bottom: none;
}

footer .promesas-valor > a:nth-child(odd) {
  border-right: 1px solid #999999;
}

footer .promesas-valor > a i {
  display: inline-block;
  vertical-align: middle;
  font-size: 34px;
}

footer .promesas-valor > a span {
  display: inline-block;
  vertical-align: middle;
  text-align: left;
  font-family: "Lato-Bold";
  margin-left: 2px;
  text-transform: uppercase;
  height: 100%;
  width: 50%;
}

footer .newsletter {
  height: 195px;
  background: url("../../assets/img/newsletter-mobile.jpg") no-repeat center;
  background-size: cover;
  text-align: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.15);
}

footer .newsletter .newsletter-header {
  color: #DF0209;
  padding-top: 17px;
}

footer .newsletter .newsletter-header i {
  font-size: 24px;
}

footer .newsletter .newsletter-header h2 {
  font-family: "Lato-Bold";
  font-size: 14px;
  margin-bottom: 8px;
  margin-top: 0;
}

footer .newsletter .newsletter-header span {
  font-family: "Lato-Regular";
  color: #000000;
  display: none;
}

footer .newsletter #newsletter-form {
  width: 65%;
  margin: auto;
}

footer .newsletter #newsletter-form .input-container {
  padding: 2px 0;
}

footer .newsletter #newsletter-form .input-container #email {
  padding: 5px;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

footer .newsletter #newsletter-form .input-container #send {
  font-family: "Lato-Bold";
  background-color: #FFED00;
  color: #DF0209;
  border: none;
  padding: 5px;
}

footer .newsletter #newsletter-form .input-container input[type="text"], footer .newsletter #newsletter-form .input-container input[type="submit"] {
  width: 100%;
}

footer .newsletter #newsletter-form .conditions-text {
  text-align: left;
  font-family: "Lato-Regular";
  font-size: 11px;
}

footer .newsletter #newsletter-form .conditions-text input[type="checkbox"] {
  margin-left: 0;
}

footer .newsletter #newsletter-form .conditions-text a {
  color: #000000;
  text-decoration: underline;
}

footer .static-links .links-section {
  border-bottom: #A70207 1px solid;
}

footer .static-links .links-section .links-header {
  padding: 9px 15px;
  margin: 0;
  color: #FFED00;
  font-family: "Lato-Bold";
  font-size: 14px;
}

footer .static-links .links-section .links-header span {
  float: right;
}

footer .static-links .links-section .links-list {
  padding: 0 25px 15px 25px;
}

footer .static-links .links-section .links-list a {
  font-family: "Lato-Regular";
  display: block;
  color: #FFFFFF;
  font-size: 12px;
}

footer .social-media {
  height: 54px;
  font-size: 20px;
  text-align: center;
}

footer .social-media a {
  color: #FFFFFF;
  line-height: 54px;
  padding: 0 5px;
}

footer .social-media a:hover {
  color: #FFED00;
}

footer .legals {
  background-color: #700105;
  color: #FFFFFF;
  height: 50px;
  line-height: 25px;
  text-align: center;
  font-family: "Lato-Light";
  font-size: 10px;
}

footer .legals .pipeline {
  display: none;
  margin: 0 3px;
}

footer .legals a {
  display: inline-block;
  position: relative;
  vertical-align: text-bottom;
  margin: 0 2px;
}

footer .legals a.blacksip-icon {
