.tabs {
  .tabs-nav {
    padding: 0;
    width: 90%;
    margin:0 auto 30px auto;
    border-bottom:1px solid $gris-letra;
    .tab-header {
      display: inline-block;
      width: calc(98%/3);
      box-sizing: border-box;
      font-weight: bold;
      padding: 8px 10px;
      cursor: pointer;
      font-family:$Lato-Bold;
      font-size: 11px;
      position: relative;
      &.active{
        &:before{
          content: ' ';
          border-bottom: 3px solid $rojo;
          width: 100%;
          position: absolute;
          bottom: -2px;
          left: 0;
        }
        &:after{
          content: ' ';
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 7px 12.5px 0 12.5px;
          border-color: #DF0209 transparent transparent transparent;
          position: absolute;
          bottom: -9px;
          left: 0;
          right: 0;
          margin: 0 auto;
        }
        a {
          color:$rojo;
        }
      }
      a{
        text-decoration: none;
        color:$gris-letra;
      }
    }
  }
  .tab-content {
    display: none;
    &.active{
      display: block;
    }
  }
}