.banner-principal{
  img{
    width: 100%;
  }
  .slick-dots {
    position: absolute;
    bottom:0;
    li{
      margin: 0px -2px;
      button:before{
        text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.15);
      }
      &:not(.slick-active) button:before {
        color: $gris;
      }
    }
  }
}


.banners-secundarios{
  margin-top: 40px;
  margin-bottom: 28px;
  img{
    padding: 0 5px;
    width:100%;
    filter: blur(3px);
    -webkit-filter: blur(3px);
    &.slick-active{
      filter: blur(0px);
      -webkit-filter: blur(0px);
    }
  }

  .slick-dots {
    li{
      margin: 0px -2px;
    }
  }
}

.banners-promos{
  div{
    padding: 0 20px;
  }
}

//TABLET
@media screen and (min-width: $tablet)  {
  .banners-secundarios{
    display: block;
    text-align: center;
    img{
      display: inline-block;
      width: 90%;
      filter: blur(0px);
      -webkit-filter: blur(0px);
    }

    &.banner-2{
      img{
        width: calc(90% / 2);
      }
    }

    &.banner-3{
      img{
        width: calc(90% / 3);
      }
    }

    &.banner-4{
      img{
        width: calc(90% / 4);
      }
    }
  }
}