body{
	margin:0;
	padding:0;
	font-size: $font-size;
}
*{
	outline: none;
}
a{
	text-decoration: none;
}

.noScroll{
	overflow: hidden;
}

button, input{
	appareance: none;
}

.anim--panUp {
	-webkit-animation: panUp .5s forwards;
	animation: panUp .5s forwards
}

.anim--panDown {
	-webkit-animation: panDown .5s forwards;
	animation: panDown .5s forwards
}

@-webkit-keyframes panUp {
	from {
		opacity: 1;
		-webkit-transform: translateY(0);
		transform: translateY(0);
		pointer-events: none
	}

	to {
		opacity: 0;
		-webkit-transform: translateY(-5%);
		transform: translateY(-5%);
		visibility: hidden;
		pointer-events: auto
	}
}

@keyframes panUp {
	from {
		opacity: 1;
		-webkit-transform: translateY(0);
		transform: translateY(0);
		pointer-events: none
	}

	to {
		opacity: 0;
		-webkit-transform: translateY(-5%);
		transform: translateY(-5%);
		visibility: hidden;
		pointer-events: auto
	}
}

@-webkit-keyframes panDown {
	0% {
		opacity: 0;
		-webkit-transform: translateY(-5%);
		transform: translateY(-5%)
	}

	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		transform: translateY(0);
		visibility: visible
	}
}

@keyframes panDown {
	0% {
		opacity: 0;
		-webkit-transform: translateY(-5%);
		transform: translateY(-5%)
	}

	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		transform: translateY(0);
		visibility: visible
	}
}